import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AppMin, } from "../core"
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-article',
  templateUrl: './article.page.html',
  styleUrls: ['./article.page.scss'],
})
export class ArticlePage implements OnInit {
  header_class;

  data = {contentImg:''};

  //输出
  title = "";
  txt;
  craet_date;
  imgUrl;
  contentImg;

  constructor(public Router: Router, public ActivatedRoute: ActivatedRoute, public appMin: AppMin, public sanitizer: DomSanitizer) {


    // this.data={
    //   contentImg:''
    // }


    if (this.appMin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class";
      console.log("判断为安卓系统 设置状态栏样式")
    }


  }

  ngOnInit() {
    this.addSpecialPage();
    //console.log(this.ActivatedRoute.snapshot.queryParams);
  }
  addSpecialPage() {
    let obj = {
      type: 'addSpecialPage',
      specialId: this.ActivatedRoute.snapshot.queryParams["id"]
    }


    this.appMin.get(obj).subscribe((data) => {
      this.dataHandle(data);
    })



  }
  assembleHTML() {
    return this.sanitizer.bypassSecurityTrustHtml(this.txt);
  }
  dataHandle(msg) {
    if (msg['type'] == 'addSpecialPage_ret') {
      console.log("addSpecialPage_ret=" + JSON.stringify(msg));
      this.title = msg['title'];
      // this.txt = msg['txt'];
      this.craet_date = msg['craet_date'];
      this.imgUrl = msg['imgUrl'];
      this.data = msg;
      this.contentImg = msg.contentImg;
      // this.objItem = msg['objItem'];
    }
  }

}
